import React, {useState, useEffect} from 'react'
import { getCatalog } from '../services/catalogService'
import ProductCard from './utils/ProductCard'
import {Container, Row, Col} from 'reactstrap'
import LoadingData from './utils/LoadingData' 
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap'

const sortFilter = ["Price Low to High", "Price High to Low", "Rating High to Low", "Rating Low to High"]

const FilterDropdown = (props) => {
    return (
        <Dropdown style={{float:'right', marginRight:'15%', marginTop:'1%', marginBottom:'1%'}} isOpen={props.dropdownOpen} toggle={props.toggle}>
            <DropdownToggle caret>
                {props.Name}
            </DropdownToggle>
            <DropdownMenu>
                {
                    props.option.map(option=>(
                        <DropdownItem onClick={props.handleFilter} style={{background:option===props.selected?"#a2c9fc":"#fff"}} value={option}>{option}</DropdownItem>
                    ))
                }
            </DropdownMenu>
        </Dropdown>
    )
}

const CatalogComponent = (props) => {

    let [catalogData, setCatalogData] = useState([])
    const [dropdownOpen, setDropdownOpen] = useState(false)
    const [filter, setFilter] = useState(null)
    const [fetchingData, setFetchingData] = useState(false)

    useEffect(()=>{
        setFetchingData(true)
        if(props.category)
            getCatalog(props.category)
            .then(res=>{
                setCatalogData(res)
            })
            .catch(err=>{
                console.log(err)
            })
            .finally(()=>{
                setFetchingData(false)
            })
    }, [props.category])

    const toggle = () => setDropdownOpen(prevState => !prevState);

    const increasingOrder = (val1, val2) => {
        return val1-val2
    }

    const decreasingOrder = (val1, val2) => {
        return val2-val1
    }

    const handleFilter = (event) => {
        setFilter(event.target.value)
        switch(event.target.value){
            case "Price Low to High": setCatalogData(catalogData.sort((productFirst, productSecond)=>{
                                        return increasingOrder(productFirst.price, productSecond.price)
                                      }))
                                      break
            case "Price High to Low": setCatalogData(catalogData.sort((productFirst, productSecond)=>{
                                            return decreasingOrder(productFirst.price, productSecond.price)
                                        }))
                                        break
            case "Rating Low to High": setCatalogData(catalogData.sort((productFirst, productSecond)=>{
                                            return increasingOrder(productFirst.rating, productSecond.rating)
                                        }))
                                        break
            case "Rating High to Low": setCatalogData(catalogData.sort((productFirst, productSecond)=>{
                                            return decreasingOrder(productFirst.rating, productSecond.rating)
                                        }))
                                        break
            default: break
        }
    }

    if(fetchingData)
        return  <LoadingData/>
    else 
        return (
                    <Container>
                        <Row>
                            <hr/>
                        </Row>
                        <Row>
                            <Col sm="8">
                            </Col>
                            <Col>
                                <FilterDropdown toggle={toggle} dropdownOpen={dropdownOpen} option={sortFilter} Name="Sort By" selected={filter} handleFilter={handleFilter}/>
                            </Col>
                        </Row>
                        <Row>
                        {
                            catalogData.map(data=>(
                                <Col>
                                    <ProductCard data={data}/>
                                </Col>
                            ))
                        }
                        {
                            catalogData.length===0 &&
                            <h2>No Data Present for selected Category.</h2>
                        }
                        </Row>
                    </Container>
     );
}
 
export default CatalogComponent;